import { CheckIcon, PlusIcon } from 'ether-ui/icons';
import React, { useEffect, useState } from 'react';
import { Button } from '../../components';

const FloatingCard: React.FC<{ delay: number; children: React.ReactNode; className?: string }> = ({ delay, children, className = '' }) => {
    const [isVisible, setIsVisible] = useState(false);

    useEffect(() => {
        const timer = setTimeout(() => setIsVisible(true), delay);
        return () => clearTimeout(timer);
    }, [delay]);

    return (
        <div
            className={`transform transition-all duration-1000 ${
                isVisible ? 'translate-y-0 opacity-100' : 'translate-y-8 opacity-0'
            } ${className}`}
        >
            {children}
        </div>
    );
};

const HeroSection: React.FC = () => {
    const [currentFeature, setCurrentFeature] = useState(0);
    const features = ['WhatsApp', 'Email', 'Instagram', 'SMS', 'Telegram'];

    useEffect(() => {
        const interval = setInterval(() => {
            setCurrentFeature((prev) => (prev + 1) % features.length);
        }, 2000);
        return () => clearInterval(interval);
    }, [features.length]);

    return (
        <div className="relative min-h-screen flex items-center justify-center overflow-hidden bg-linear-to-br from-purple-50 via-white to-blue-50">
            {/* Animated Background Elements */}
            <div className="absolute inset-0">
                <div className="absolute top-20 left-10 w-64 h-64 bg-linear-to-r from-purple-300 to-blue-300 rounded-full mix-blend-multiply filter blur-xl opacity-20 animate-pulse"></div>
                <div
                    className="absolute bottom-20 right-10 w-80 h-80 bg-linear-to-r from-blue-300 to-purple-300 rounded-full mix-blend-multiply filter blur-xl opacity-20 animate-pulse"
                    style={{ animationDelay: '1s' }}
                ></div>
            </div>

            <div className="container mx-auto px-6 lg:px-8 relative z-10">
                <div className="grid lg:grid-cols-2 gap-12 items-center">
                    {/* Left Content */}
                    <div className="text-center lg:text-left">
                        <FloatingCard delay={0}>
                            <div className="inline-flex items-center px-4 py-2 bg-white rounded-full shadow-lg mb-6">
                                <span className="w-2 h-2 bg-green-500 rounded-full mr-2 animate-pulse"></span>
                                <span className="text-sm font-medium text-gray-700">Trusted by 10,000+ businesses</span>
                            </div>
                        </FloatingCard>

                        <FloatingCard delay={200}>
                            <h1 className="text-4xl lg:text-6xl font-bold text-gray-900 mb-6 leading-tight">
                                Unleash the Power of
                                <span className="block bg-linear-to-r from-purple-600 to-blue-600 bg-clip-text text-transparent">
                                    OmniBlast Marketing
                                </span>
                            </h1>
                        </FloatingCard>

                        <FloatingCard delay={400}>
                            <div className="text-xl text-gray-600 mb-8 min-h-[60px] flex items-center justify-center lg:justify-start">
                                <span>
                                    Send targeted campaigns across{' '}
                                    <span className="font-semibold bg-linear-to-r from-purple-600 to-blue-600 bg-clip-text text-transparent transition-all duration-500">
                                        {features[currentFeature]}
                                    </span>{' '}
                                    and more with AI-powered automation
                                </span>
                            </div>
                        </FloatingCard>

                        <FloatingCard delay={600}>
                            <div className="flex flex-col sm:flex-row gap-4 justify-center lg:justify-start mb-8">
                                <Button
                                    href="/register"
                                    className="bg-linear-to-r from-purple-600 to-blue-600 hover:from-purple-700 hover:to-blue-700 text-white px-8 py-4 rounded-xl font-semibold text-lg shadow-xl hover:shadow-2xl transform hover:scale-105 transition-all duration-200"
                                >
                                    Start Free Trial
                                </Button>
                                <Button
                                    href="#features"
                                    className="border-2 border-gray-300 hover:border-purple-600 text-gray-700 hover:text-purple-600 px-8 py-4 rounded-xl font-semibold text-lg transition-all duration-200"
                                >
                                    View Features
                                </Button>
                            </div>
                        </FloatingCard>

                        <FloatingCard delay={800}>
                            <div className="flex items-center justify-center lg:justify-start space-x-6 text-sm text-gray-500">
                                <div className="flex items-center">
                                    <CheckIcon className="w-4 h-4 text-green-500 mr-2" />
                                    No setup fees
                                </div>
                                <div className="flex items-center">
                                    <CheckIcon className="w-4 h-4 text-green-500 mr-2" />
                                    14-day free trial
                                </div>
                                <div className="flex items-center">
                                    <CheckIcon className="w-4 h-4 text-green-500 mr-2" />
                                    Cancel anytime
                                </div>
                            </div>
                        </FloatingCard>
                    </div>

                    {/* Right Visual */}
                    <div className="relative">
                        <FloatingCard delay={1000}>
                            <div className="relative">
                                {/* Main Dashboard Mockup */}
                                <div className="bg-white rounded-2xl shadow-2xl p-6 transform rotate-3 hover:rotate-0 transition-transform duration-500">
                                    <div className="flex items-center mb-4">
                                        <div className="w-3 h-3 bg-red-500 rounded-full mr-2"></div>
                                        <div className="w-3 h-3 bg-yellow-500 rounded-full mr-2"></div>
                                        <div className="w-3 h-3 bg-green-500 rounded-full"></div>
                                    </div>
                                    <div className="space-y-4">
                                        <div className="h-4 bg-linear-to-r from-purple-200 to-blue-200 rounded w-3/4"></div>
                                        <div className="h-3 bg-gray-200 rounded w-full"></div>
                                        <div className="h-3 bg-gray-200 rounded w-5/6"></div>
                                        <div className="grid grid-cols-3 gap-2 mt-4">
                                            <div className="h-16 bg-linear-to-r from-purple-100 to-blue-100 rounded-lg"></div>
                                            <div className="h-16 bg-linear-to-r from-blue-100 to-purple-100 rounded-lg"></div>
                                            <div className="h-16 bg-linear-to-r from-purple-100 to-blue-100 rounded-lg"></div>
                                        </div>
                                    </div>
                                </div>

                                {/* Floating Elements */}
                                <FloatingCard delay={1200} className="absolute -top-4 -left-4">
                                    <div className="bg-white rounded-lg shadow-lg p-3 border border-green-200">
                                        <div className="flex items-center text-sm">
                                            <CheckIcon className="w-4 h-4 text-green-500 mr-2" />
                                            Message Sent
                                        </div>
                                    </div>
                                </FloatingCard>

                                <FloatingCard delay={1400} className="absolute -bottom-4 -right-4">
                                    <div className="bg-white rounded-lg shadow-lg p-3 border border-blue-200">
                                        <div className="flex items-center text-sm">
                                            <PlusIcon className="w-4 h-4 text-blue-500 mr-2" />
                                            New Lead
                                        </div>
                                    </div>
                                </FloatingCard>
                            </div>
                        </FloatingCard>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default HeroSection;
