import React, { useEffect, useState } from 'react';

interface Testimonial {
    name: string;
    company: string;
    role: string;
    content: string;
    rating: number;
    image: string;
}

const TestimonialCard: React.FC<{ testimonial: Testimonial; isActive: boolean }> = ({ testimonial, isActive }) => (
    <div className={`transform transition-all duration-700 ${isActive ? 'scale-100 opacity-100' : 'scale-95 opacity-70'}`}>
        <div className="bg-white rounded-2xl shadow-xl p-8 max-w-2xl mx-auto">
            {/* Stars */}
            <div className="flex mb-6">
                {[...Array(testimonial.rating)].map((_, i) => (
                    <span key={i} className="text-yellow-400 text-xl">
                        ‚≠ê
                    </span>
                ))}
            </div>

            {/* Quote */}
            <blockquote className="text-lg text-gray-700 mb-8 italic leading-relaxed">"{testimonial.content}"</blockquote>

            {/* Author */}
            <div className="flex items-center">
                <div className="w-12 h-12 bg-linear-to-r from-purple-500 to-blue-500 rounded-full flex items-center justify-center text-white font-bold text-lg mr-4">
                    {testimonial.name
                        .split(' ')
                        .map((n) => n[0])
                        .join('')}
                </div>
                <div>
                    <div className="font-semibold text-gray-900">{testimonial.name}</div>
                    <div className="text-gray-600 text-sm">
                        {testimonial.role} at {testimonial.company}
                    </div>
                </div>
            </div>
        </div>
    </div>
);

const TestimonialsSection: React.FC = () => {
    const [currentTestimonial, setCurrentTestimonial] = useState(0);

    const testimonials: Testimonial[] = [
        {
            name: 'Sarah Johnson',
            company: 'TechCorp Solutions',
            role: 'Marketing Director',
            content:
                'OmniBlast transformed our marketing game completely. The WhatsApp integration alone increased our engagement by 400%. The AI-powered content generation saves us hours every week.',
            rating: 5,
            image: 'SJ',
        },
        {
            name: 'Michael Chen',
            company: 'E-commerce Plus',
            role: 'Growth Manager',
            content:
                "We've tried many marketing automation tools, but OmniBlast's multi-channel approach is unmatched. Our conversion rates improved by 250% within the first month.",
            rating: 5,
            image: 'MC',
        },
        {
            name: 'Emily Rodriguez',
            company: 'StartupHub',
            role: 'Founder',
            content:
                'As a startup, we needed something powerful yet affordable. OmniBlast delivered beyond our expectations. The analytics dashboard gives us insights we never had before.',
            rating: 5,
            image: 'ER',
        },
        {
            name: 'David Park',
            company: 'RetailMax',
            role: 'Digital Marketing Lead',
            content:
                'The template management system for WhatsApp is incredible. We can manage hundreds of campaigns effortlessly. Customer support is also outstanding!',
            rating: 5,
            image: 'DP',
        },
    ];

    useEffect(() => {
        const interval = setInterval(() => {
            setCurrentTestimonial((prev) => (prev + 1) % testimonials.length);
        }, 5000);

        return () => clearInterval(interval);
    }, [testimonials.length]);

    return (
        <div className="py-20 bg-gray-50">
            <div className="container mx-auto px-6">
                <div className="text-center mb-16">
                    <h2 className="text-4xl lg:text-5xl font-bold text-gray-900 mb-6">
                        Loved by businesses
                        <span className="block bg-linear-to-r from-purple-600 to-blue-600 bg-clip-text text-transparent">
                            around the world
                        </span>
                    </h2>
                    <p className="text-xl text-gray-600 max-w-2xl mx-auto">
                        See what our customers say about their experience with OmniBlast
                    </p>
                </div>

                {/* Main Testimonial */}
                <div className="mb-12">
                    <TestimonialCard testimonial={testimonials[currentTestimonial]} isActive={true} />
                </div>

                {/* Testimonial Navigation */}
                <div className="flex justify-center space-x-3 mb-12">
                    {testimonials.map((_, index) => (
                        <button
                            key={index}
                            onClick={() => setCurrentTestimonial(index)}
                            className={`w-3 h-3 rounded-full transition-all duration-300 ${
                                index === currentTestimonial ? 'bg-purple-600 scale-125' : 'bg-gray-300 hover:bg-gray-400'
                            }`}
                        />
                    ))}
                </div>

                {/* Stats Grid */}
                <div className="grid md:grid-cols-3 gap-8 max-w-4xl mx-auto">
                    <div className="text-center p-6 bg-white rounded-2xl shadow-lg">
                        <div className="text-3xl font-bold text-purple-600 mb-2">4.9/5</div>
                        <div className="text-gray-600">Customer Rating</div>
                        <div className="flex justify-center mt-2">
                            {[...Array(5)].map((_, i) => (
                                <span key={i} className="text-yellow-400">
                                    ‚≠ê
                                </span>
                            ))}
                        </div>
                    </div>

                    <div className="text-center p-6 bg-white rounded-2xl shadow-lg">
                        <div className="text-3xl font-bold text-blue-600 mb-2">24/7</div>
                        <div className="text-gray-600">Support Available</div>
                        <div className="text-sm text-gray-500 mt-2">Always here to help</div>
                    </div>

                    <div className="text-center p-6 bg-white rounded-2xl shadow-lg">
                        <div className="text-3xl font-bold text-green-600 mb-2">99.9%</div>
                        <div className="text-gray-600">Uptime Guarantee</div>
                        <div className="text-sm text-gray-500 mt-2">Reliable service</div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default TestimonialsSection;
