import React from 'react';
import './style.css';

const PartnersSection: React.FC = () => {
    const partners = [
        { name: 'WhatsApp Business', logo: 'WB' },
        { name: 'SendGrid', logo: 'SG' },
        { name: 'Twilio', logo: 'TW' },
        { name: 'Instagram', logo: 'IG' },
        { name: 'Telegram', logo: 'TG' },
        { name: 'AWS SNS', logo: 'AWS' },
        { name: 'Mailgun', logo: 'MG' },
        { name: 'Gupshup', logo: 'GS' },
    ];

    return (
        <div className="py-16 bg-gray-50">
            <div className="container mx-auto px-6">
                <div className="text-center mb-12">
                    <h3 className="text-lg font-semibold text-gray-600 mb-8">Trusted integrations with leading platforms</h3>

                    <div className="relative overflow-hidden">
                        <div className="flex animate-slide space-x-12">
                            {/* First set */}
                            {partners.map((partner, index) => (
                                <div
                                    key={`first-${index}`}
                                    className="shrink-0 w-24 h-16 bg-white rounded-lg shadow-md flex items-center justify-center hover:shadow-lg transition-shadow duration-200"
                                >
                                    <span className="text-sm font-bold text-gray-600">{partner.logo}</span>
                                </div>
                            ))}

                            {/* Duplicate set for seamless loop */}
                            {partners.map((partner, index) => (
                                <div
                                    key={`second-${index}`}
                                    className="shrink-0 w-24 h-16 bg-white rounded-lg shadow-md flex items-center justify-center hover:shadow-lg transition-shadow duration-200"
                                >
                                    <span className="text-sm font-bold text-gray-600">{partner.logo}</span>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default PartnersSection;
